@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;1,500&display=swap);
:root{--color-white: #fff;--color-black: #000;--color-grey-light-1: #faf9f9;--color-grey-light-2: #f4f2f2;--color-grey-light-3: #f8eeee;--color-grey-light-4: #ccc;--grey-dark-1: #333;--grey-dark-2: #777;--grey-dark-3: #999;--color-green: #1ed760 !important;--color-light-blue: rgb(150, 240, 182);--color-blue: #202f72;--color-section-blue: rgb(41, 65, 171);--color-dark-pink: #f46ebe;--shadow-dark: 0 2rem 6rem rgba(#000, 0.3)}*{margin:0;padding:0}*,*::before,*::after{box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%}body{font-weight:400;font-family:"Poppins", sans-serif;line-height:1.7}input[type="checkbox"]{display:none}.hidden{display:none;opacity:0}

.nav{height:8rem;width:100vw;background:var(--color-black);padding:2rem 7rem;display:flex;justify-content:space-between;align-items:center;color:var(--color-white)}@media (max-width: 75em){.nav{padding:1.5rem 6rem}}@media (max-width: 56.25em){.nav{padding:1.5rem 3rem}}.nav__logo{width:5rem;height:1rem;opacity:0;-webkit-transform:scale(0.5);transform:scale(0.5)}@media (max-width: 56.25em){.nav__logo{width:10rem}}.nav__right{display:flex;align-items:center;justify-content:space-between;position:relative}@media (max-width: 56.25em){.nav__right{min-width:8vw}}.nav__item{font-size:1.5rem;font-weight:600;margin:0 1.5rem;cursor:pointer}.nav__item:nth-child(6),.nav__item:nth-child(5){width:6vw}.nav__item:hover{color:var(--color-green)}@media (max-width: 75em){.nav__item{opacity:0;visibility:hidden}}.nav__response{height:1.4rem;width:1.8px;background:var(--color-white);position:relative;cursor:pointer;z-index:100}.nav__response::before,.nav__response::after{position:absolute;width:100%;height:100%;left:0;background:var(--color-white);transition:all 0.2s}.nav__response::before{top:-7px}.nav__response::after{top:7px}@media (max-width: 75em){.nav__response{opacity:1;visibility:visible;width:2rem;height:2px;border-radius:1rem;position:absolute;right:0}.nav__response::before,.nav__response::after{content:""}}.nav__input:checked ~ .nav__right .nav__response{background:transparent}.nav__input:checked ~ .nav__right .nav__response::before{-webkit-transform:rotate(135deg);transform:rotate(135deg)}.nav__input:checked ~ .nav__right .nav__response::after{-webkit-transform:rotate(-135deg);transform:rotate(-135deg)}.nav__input:checked ~ .nav__right .nav__response::after,.nav__input:checked ~ .nav__right .nav__response::before{top:0}.nav__input:checked ~ .nav__responsive{display:block;opacity:1}

.navbar{position:absolute;top:0;left:0;display:flex;color:var(--color-white);font-size:2.5rem;font-weight:600}.navbar__left,.navbar__right{min-height:100vh;width:50vw;padding:2rem 4rem}.navbar__left{background:rgba(0,0,0,0.75);flex:1 1 50%}.navbar__right{flex:1 0 50%;background:var(--color-black);display:flex;flex-direction:column}@media (max-width: 37.5em){.navbar__right{position:absolute;top:0;width:100vw}}.navbar__item{margin-top:2rem;cursor:pointer}.navbar__item:nth-child(5){margin-top:5rem}.navbar__item:not(:nth-child(4)):hover{color:var(--color-green)}.navbar__divider{margin-top:3rem;border-bottom:2px solid var(--color-white);width:2rem}

.btn{text-decoration:none;font-size:1.6rem;font-weight:900;text-transform:uppercase;text-align:center;letter-spacing:0.2rem;word-spacing:0.5rem;display:inline-block;padding:1rem 3rem;border-radius:100rem;cursor:pointer;transition:all 0.2s}.btn:hover{-webkit-transform:translateY(-3px) scale(1.1);transform:translateY(-3px) scale(1.1)}

[class^="col-"]{float:left}[class^="col-"]:not(:last-child){margin-right:60px}.row{max-width:1140px;margin:0 auto}.row::after{content:"";clear:both;display:table}.row:not(:last-child){margin-bottom:80px}.row .col-1-of-2{width:calc((100% - 60px) / 2);float:left}.row .col-1-of-2:not(:last-child){margin-right:60px}.row .col-1-of-3{width:calc((100% - 2 *60px) / 3);float:left}.row .col-1-of-3:not(:last-child){margin-right:60px}.row .col-2-of-3{width:calc(2 *calc((100% - 2 *60px) / 3) + 60px)}.row .col-1-of-4{width:calc((100% - 3 *60px) / 4)}.row .col-2-of-4{width:calc(2 *calc((100% - 3 *60px) / 4) + 60px)}.row .col-3-of-4{width:calc(3 *calc((100% - 3 *60px) / 4) + 2 *60px)}.subscription{color:var(--color-blue)}.subscription:nth-child(3){color:var(--color-green)}.heading-primary{font-size:5rem;line-height:1.2;margin:0;padding:0;font-weight:900;max-width:100%;width:auto;display:inline-block}.heading-secondary{font-size:2.4rem;margin:3rem 0}.heading-tetiary{font-size:1.4rem;margin:1.6rem 0 3.2rem;text-transform:uppercase}.heading-smallest{max-width:63rem;font-size:1rem}.subscription{min-height:87vh;padding:10rem}.buttonSpace{margin:3rem 0}

